import { ModalGet } from "../../../../utils/modal/modal-get.js";
import { BaseW3CSS } from "../../base/componente-w3css.js";
const template = /*html*/`


<div class="w3-row" no-usar>
    <div class="w3-col s12 w3-center">
    <button class="w3-button w3-blue">Cargar video</button>
    </div>
</div>

<div class="w3-row w3-margin-top" video>
<div class="w3-col s2 espacio" propiedades espacio-componente></div>
<video id="video" propiedades
    src="https://www.w3schools.com/html/mov_bbb.mp4" 
    controls="controls"
    class="w3-col s8"
    contenido-componente
    >        
</video> 
</div>


`;
const style = `
.cls-imagen{
    width: 100%;
    border-radius: 5px;
}
`;
class VideoComponente extends BaseW3CSS {
    constructor() {
        super();
        this.render();
    }

    render() {
        super.render();
        let shadowRoot = this.shadowRoot;
        shadowRoot.innerHTML += `            
            <style>${style}</style>
            ${template}
        `;
        this.addListeners();            
    }

    addListeners(){
        let  btn = this.shadowRoot.querySelector('button');        
        btn.addEventListener('click', this.onChangeImg.bind(this));
    }

    onChangeImg(){
        const video = this.shadowRoot.querySelector('video');
        let dlg = new ModalGet('Dame url de imagen', video.src);
        dlg.open().then(dlg => {            
            video.src = dlg.data;
            video.load();
        });
    }

    getHtml() {
        const div = this.shadowRoot.querySelector('[video]')    
        return this.getInnerHtml(div);
    }

    getStyle() {
        return style;
    }

    getScrip() {
        return '';
    }

    getAngularTemplate() {
        const original = this.shadowRoot.querySelector('[video]')    
        const div = original.cloneNode(true);
        return this.getInnerHTMLAngular(div);
    }

    addAllListenersShadowRoot() {
        this.addListenersPropiedades();
        this.addListeners();
    }
}

customElements.define("x-video", VideoComponente);